<template>
  <div>
    <div>
      <p class="bar-item"> </p>
      <ul class="setting-list">
        <li class="item" @click.stop='clearCache'></li>
        <li class="item" @click.stop='checkUpdate'></li>
      </ul>
      <hr>
      <p class="bar-item"> </p>
      <ul class="setting-list">
        <router-link to="/me/recorder">
          <li class="item"></li>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    methods: {
      async clearCache() {
        // localStorage
        this.$store.dispatch('reset')
        if (navigator.serviceWorker) {
          //  service worker
          let r = await navigator.serviceWorker.getRegistrations().then(function (registrations) {
            for (let registration of registrations) {
              registration.unregister()
            }
          })
          let fs = await window.FileSystem.getInstance()
          if (fs && typeof fs.root.remove === 'function') {
            r = await fs.remove()
          }
          alert('')
        }
      },
      checkUpdate() {
        this.$store.commit('checkUpdate')
      }
    }
  }

</script>

<style scoped>

</style>


