/**
 * ,http://www.cnblogs.com/beiz/p/5817192.html
 * @param {Number} angx x 
 * @param {Number} angy y 
 */
function getAngle(angx, angy) {
  return Math.atan2(angy, angx) * 180 / Math.PI
}

/**
 *  1 2 3 4 0 ,http://www.cnblogs.com/beiz/p/5817192.html
 * @param {Number} startx x
 * @param {Number} starty y
 * @param {Number} endx   x
 * @param {Number} endy   y 
 * @param {Number} dx     x
 * @param {Number} dy     y
 */
function getDirection(startx, starty, endx, endy, dx = 20, dy = 20) {
  let angx = endx - startx
  let angy = endy - starty
  let result = 0

  // 
  if (Math.abs(angx) < dx && Math.abs(angy) < dy) {
    return result
  }

  var angle = getAngle(angx, angy)
  if (angle >= -135 && angle <= -45) {
    result = 1
  } else if (angle > 45 && angle < 135) {
    result = 2
  } else if ((angle >= 160 && angle <= 180) || (angle >= -180 && angle < -160)) {
    result = 3
  } else if (angle >= -20 && angle <= 20) {
    result = 4
  }
  return result
}

export const detect = function (startPoint, endPoint, options = {}) {
  return getDirection(startPoint.pageX, startPoint.pageY, endPoint.pageX, endPoint.pageY, options.dx, options.dy)
}




