import { proxy } from '../utils/LSProxy'
let state = {
  currentTime: 0,
  //   0 1 playing, 2 pause
  state: 0,
  //  0 12
  mode: 0
}
// state = proxy(state, 'player', ['mode'])

const mutations = {

  reset(state) {
    state.currentTime = 0
    state.state = 0
    state.mode = 0
  },
  /**
   * 
   * @param {*} state 
   * @param {*} currentTime 
   */
  timeUpdate(state, currentTime) {
    state.currentTime = currentTime
  },
  /**
   * 
   * @param {*} state 
   * @param {*} val  0 12
   */
  setState(state, val) {
    state.state = val
  }
}

export default {
  namespaced: true,
  state,
  mutations
}


